<template>
    <div class="select-cate">
        <div class="first-cate">
            <div>{{ list.category_name }}</div>
        </div>

        <div class="second-cate">
            <el-select v-model="secondCate" 
            placeholder="请选择" 
            @change="changethirdData">
                <el-option 
                v-for="item in list.list" 
                :key="item.category_id" 
                :label="item.category_name" 
                :value="item.category_id">
                </el-option>
            </el-select>
        </div>

        <div class="third-cate" v-if="isShowThird">
            <el-select v-model="thirdCate" 
            placeholder="请选择" 
            :disabled="disabled" 
            @change="sendId">
                <el-option 
                v-for="item in thirdData.list" 
                :key="item.category_id" 
                :label="item.category_name" 
                :value="item.category_id">
                </el-option>
            </el-select>
        </div>

        <div class="openCategory" @click="openCategory">
          + 添加商品分类
        </div>
        <span class="tip" v-if="isShowThird">注：请选择到三级分类，若无，请添加三级分类</span>
    </div>
</template>

<script>
import { getCategory, getFoodEditor } from "../api";
import { GoodList } from "../index";
import { GoodType } from "@/config/cfg";

export default {
  props: {
    // categoryArr: {
    //   type: Array,
    //   default: function() {
    //     return [{ category_id: "" }, { category_id: "" }, { category_id: "" }];
    //   }
    // },
    getcategoryType: {
      type: Number
    }
  },
  data() {
    return {
      secondCate: "",
      thirdCate: "",
      cateById: {},
      disabled: true,
      isShowThird: true,
      thirdData: {
        category_id: "301",
        category_name: "热菜",
        list: [
          {
            category_id: "539",
            category_name: "川菜",
            list: []
          }
        ]
      },
      list: {
        category_id: "11",
        category_name: "菜品",
        list: [
          {
            category_id: "301",
            category_name: "热菜",
            list: [
              {
                category_id: "539",
                category_name: "川菜",
                list: []
              }
            ]
          }
        ]
      },
      categoryArr: [],

      isClearThireid: true
    };
  },
  created() {
    let isnewAccess = this.$route.query.isnewAccess;
    if (isnewAccess) {
      this.isShowThird = false;
    }
    
    this.getCategorys();
  },
  watch: {
    // categoryArr: {
    //   handler: function(val) {
    //     // this.init();
    //   },
    //   deep: true
    // }
  },
  methods: {
    //拉取分类
    getCategorys() {
      let data = {
        type_list: 1,
        type: this.getcategoryType
      };

      getCategory(data, resp => {
        this.list = resp.data.list;
        this.cateById = this.getCateById(this.list.list);

        // this.init();
        this.getFoodinfoCate();
      });
    },
    //拉取对应foodid的分类
    getFoodinfoCate() {
      let foodid = this.$route.query.foodId;

      if (!GoodList.isEmpty(foodid)) {
        getFoodEditor(foodid, resp => {
          if (resp.ret === 0) {
            let foodinfo = resp.data.info || {};
            this.categoryArr = foodinfo.category || [];

            this.init();
          } else {
            console.warn("获取菜品详情分类失败");
          }
        });
      }
    },

    /**
     * 建立分类ID索引  
     */
    getCateById(cateData = []) {
      let cateById = {};

      const _saveCate = item => {
        item.forEach(items => {
          if (items.hasOwnProperty("category_id")) {
            cateById[items.category_id] = items;
          }

          if (items.hasOwnProperty("list") && items.list.length > 0) {
            _saveCate(items.list);
          }
        });
      };

      _saveCate(cateData);
      return cateById;
    },

    changethirdData() {
      this.disabled = false;

      if (this.isClearThireid) {
        this.thirdCate = "";
      } else {
        this.isClearThireid = true;
      }

      this.thirdData = this.cateById[this.secondCate] || {};
      this.$emit("on-changeT", this.secondCate);
    },
    sendId() {
      if (this.thirdCate === "") {
        return;
      }
      this.$emit("on-change", this.thirdCate);
    },
    init() {
      this.isClearThireid = false;
      this.secondCate = (this.categoryArr[1] || {}).category_id || "";
      this.thirdCate = (this.categoryArr[2] || {}).category_id || "";

      if (this.getcategoryType === GoodType.ACCESSORY) {
        this.isShowThird = false;
      }
    },
    openCategory() {
      this.$emit("on-opencate");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.select-cate > div {
  // display: inline-block;
  float: left;
  margin-right: 10px;
}

.first-cate > div {
  @include wh(50px, 34px);
  line-height: 34px;
  background-color: $graybg;
  text-align: center;
}

.select-cate .tip {
  color: #e7487e;
}
.openCategory {
  @include wh(150px, 34px);
  text-align: center;
  line-height: 34px;
  color: $blueA;
  border: 1px solid $blueA;
  cursor: pointer;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

.select-cate {
  .el-select {
    width: 180px;

    .el-input__inner {
      height: 34px !important;
      border-radius: 0;
    }
  }
}
</style>





